import React, { Component } from 'react';
import Link from 'next/link';
import Button from '../buttons/button';
import Logo from '../logo-2/logo';
import './header.scss';
import classnames from 'classnames';
import BtnResp from '../btn-resp/btn-resp';
import Notifications from '../notifications/notifications';
import { connect } from 'react-redux';

class Header extends Component{
  constructor(props){
    super(props);
    this.state = {
      down: false
    }
  }

  componentDidMount() {
    window.onscroll = () => {
      if(window.scrollY > 50) {
        this.setState({ down: true });
      } else {
        this.setState({ down: false });
      }
    }
  }

  render(){
    const { down } = this.state;
    const { isLogin } = this.props;
    return(
      <header className={classnames({
        scroll_header: down,
        header_hero_main: true,
        shadow: down,
      })}>
        <div className="container-fluid header_main_cont">
          <Logo />
          <nav>
            {
              isLogin 
              ? <Notifications />
              : <Button color="light">Login</Button>
            }
          </nav>
        </div>
      </header>
    );
  }
};

const mapStateToProps = state => ({ isLogin: state.login });

export default connect(mapStateToProps)(Header);

